        > -->
        <van-field name="uploader" label="上传新头像">
          <template #input>
            <van-uploader
              v-model="fileList"
              :before-read="beforeRead"
              accept="image/jpeg,image/jpg,image/png"
              multiple
              :max-count="1"
            />
          </template>
        </van-field>
        <van-field
          v-model="userInfo.mobile"
          readonly
          name="mobile"
          label="联系方式"
          maxlength="15"
        />
        <van-field
          v-model="userInfo.email"
          readonly
          name="email"
          label="电子邮件"
