          :rules="[{ required: true, message: '请输入新密码' }]"
        />
        <van-field
          v-model="editPasswordForm.rePass"
          type="password"
          name="rePass"
          maxlength="20"
          label="确认新密码"
          placeholder="确认新密码"
          :rules="[
            { required: true, message: '请确认新密码' },
            { validator: this.valideRePassword, trigger: 'blur' }
          ]"
        />
        <SecurityCode
          :value.sync="value"
          :biyiCaptchaKey.sync="biyiCaptchaKey"
          ref="securityCode"
        />
      </van-form>
    </van-dialog> -->
  </div>
</template>
<script>
import { updateUser } from '@/api/userInfo' // 查询
import { ImagePreview } from 'vant'
// import { updatePswd } from '@/api/login'
// import SecurityCode from '@/components/SecurityCode.vue'
export default {
  name: 'PersonalCenter',
  data() {
    return {
      editPasswordModal: false,
      isReadonly: true,
      oldPass: '',
      newPass: '',
      rePass: '',
      userInfo: {},
      fileList: [],
      editPasswordForm: {
        oldPass: '',
        newPass: '',
        rePass: ''
      },
      compressionImg: '',
      value: '',
      biyiCaptchaKey: ''
    }
  },
  methods: {
    ImageView(img) {
      ImagePreview([img])
    },
    valideRePassword(value) {
      if (value !== this.editPasswordForm.newPass) {
        this.$notify({
          message: '两次输入密码不一致'
        })
        return false
      } else {
        return true
      }
    },
    logout() {
      this.$store.commit('user/logout')
      window.location.reload()
    },
    // 验证图片格式是否正确和压缩图片
    async beforeRead(file) {
      const isJpgOrPng =
        file.type === 'image/jpeg' ||
        file.type === 'image/png' ||
        file.type === 'mage/jpg'
      if (isJpgOrPng) {
        this.compressionImg = await this.$util.iosRotateImg(file)
        this.handleSubmit()
      } else {
        return new Promise((reject) => {
          this.$toast({
            message: '请选择图片格式文件!',
            forbidClick: true
          })
          this.compressionImg = ''
          return reject(new Error(false))
        })
      }
    },
    // onBeforeClose(action, done) {
    //   if (action === 'confirm') {
    //     this.$refs.updPws
    //       .validate()
    //       .then(() => {
    //         // 验证通过
    //         // 修改密码提交

    //         updatePswd(
    //           {
    //             userName: this.userInfo.username,
    //             oldPassword: this.editPasswordForm.oldPass,

    //             newPassword: this.editPasswordForm.newPass
    //           },
    //           {
    //             biyiCaptchaKey: this.biyiCaptchaKey,
    //             biyiCaptcha: JSON.stringify({
    //               code: this.value
    //             })
    //           }
    //         )
    //           .then((response) => {
    //             if (response.data.token) {
    //               this.$toast.success('更新密码成功')
    //               this.editPasswordModal = false
    //               this.$store.commit('user/logout')
    //               this.$router.push({ name: 'login' })
    //               done()
    //             } else {
    //               this.$refs.securityCode.init()
    //               this.$notify({
    //                 message: `更新密码失败：${response.data.msg}`
    //               })
    //               done(false)
    //             }
    //           })
    //           .catch((error) => {
    //             done(false)
    //             this.$refs.securityCode.init()
    //             if (error.response) {
    //               this.$notify({
    //                 message: `更新密码失败：${error.response.data.message}！`
    //               })
    //             }
    //           })
    //       })
    //       .catch(() => {
    //         done(false)
    //         //验证失败
    //       })
    //   } else {
    //     done()
    //   }
    // },
    showEditPassword() {
      this.editPasswordForm = {
        oldPass: '',
        newPass: '',
        rePass: ''
      }
      this.editPasswordModal = true
    },
    cancelButton() {
      this.editPasswordForm = {
        oldPass: null,
        newPass: null,
        rePass: null
      }
      this.editPasswordModal = false
    },
    handleSubmit() {
      // 点击提交修改后的基本信息
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: '头像更新中...'
      })
      const imgPath = this.compressionImg || this.userInfo.imgPath
      updateUser({
        ...this.userInfo,
        imgPath
        // newPassword: params.newPassword
      })
        .then((res) => {
          this.$toast.clear()
          if (res.status === 200) {
            this.userInfo.imgPath = imgPath
            // 更新状态
            this.$store.commit('user/setUserInfo', this.userInfo)
            this.$toast.success('更新信息成功')
          } else {
            this.$toast.fail('更新信息失败')
          }
        })
        .catch(() => {
          this.$toast.clear()
          this.$toast.fail('更新信息失败')
        })
    }
  },
  created() {
    this.$store.dispatch('user/GetInfo').then((res) => {
      if (res.status === 200) {
        this.userInfo = res.data
      }
    })
  }
  // components: {
  //   SecurityCode
  // }
}
</script>

<style lang="less" scoped>
.title {
  position: absolute;
  top: 30px;
  left: 12px;
  right: 10px;
  // z-index: 10;
  display: flex;
  justify-content: start;
  align-items: center;
  color: #ffffffc9;
  .van-image {
    width: 80px;
    height: 80px;
    margin-right: 10px;
  }
}
.detail-top {
  height: 123.95px;
}
.log-out {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50px;
  right: 0;
  width: 80px;
  height: 38px;
  background: rgba(48, 157, 236, 0.19);
  border-radius: 16px 0px 0px 16px;
  .logout-title {
    font-size: 14px;
    color: #cceeff;
  }
}
.edit-password {
  margin-left: 15px;
  margin-bottom: 10px;
}
.submit-btn {
